import { SubSkill } from "@/types/subSkill";
import { subSkillData } from "@/data/subSkillData";
import {
  hasVariants,
  getAvailableVariants,
  getRarityStyles,
  VARIANT_CONFIG,
  getRarityByVariant,
} from "@/utils/subSkillUtils";

interface SkillListViewProps {
  onSkillSelect: (skill: SubSkill) => void;
}

export const SkillListView = ({ onSkillSelect }: SkillListViewProps) => {
  // スキルグループ毎にグループ化
  const groupedSkills = subSkillData.reduce(
    (acc, skill) => {
      if (skill.skillGroup && hasVariants(skill)) {
        if (!acc.withVariants[skill.skillGroup]) {
          acc.withVariants[skill.skillGroup] = [];
        }
        acc.withVariants[skill.skillGroup].push(skill);
      } else {
        acc.withoutVariants.push(skill);
      }
      return acc;
    },
    {
      withVariants: {} as Record<string, SubSkill[]>,
      withoutVariants: [] as SubSkill[],
    }
  );

  // バリアントなしスキルの表示用カード
  const renderSkillCard = (skill: SubSkill) => {
    const styles = getRarityStyles(skill.rarity);

    return (
      <button
        key={skill.name}
        onClick={() => onSkillSelect(skill)}
        className={`
          p-2 rounded-lg border-2
          ${styles.gradient} ${styles.border}
          ${styles.hover}
          transition-all active:scale-95
          text-center
        `}
      >
        <div className="text-xs font-medium text-gray-800 break-words">
          {skill.displayName}
        </div>
      </button>
    );
  };

  // バリアントありスキルの表示用カード（グループごと）
  const renderVariantSkillCard = (skillGroup: string, skills: SubSkill[]) => {
    const config = VARIANT_CONFIG[skillGroup];
    const variants = getAvailableVariants(skillGroup);

    return (
      <button
        key={skillGroup}
        onClick={() => onSkillSelect(skills[0])} // グループの代表スキルを渡す
        className="
          p-2 rounded-lg border-2
          bg-white border-gray-300
          hover:bg-gray-50 hover:border-gray-400
          transition-all active:scale-95
          text-center relative
        "
      >
        <div className="text-xs font-medium text-gray-800 break-words mb-1.5">
          {config.label}
        </div>
        {/* バリアントタグ */}
        <div className="flex gap-1 justify-center flex-wrap">
          {variants.map((variant) => {
            const variantRarity = getRarityByVariant(skillGroup, variant);
            const variantStyles = getRarityStyles(variantRarity);
            return (
              <span
                key={variant}
                className={`
                  ${variantStyles.chip}
                  px-1.5 py-0.5 rounded text-[9px] font-semibold
                `}
              >
                {variant}
              </span>
            );
          })}
        </div>
      </button>
    );
  };

  return (
    <div className="space-y-2">
      <h3 className="text-base font-bold text-gray-800">スキルを選択</h3>

      <div className="grid grid-cols-3 gap-2">
        {/* バリアントなしスキル */}
        {groupedSkills.withoutVariants.map((skill) => renderSkillCard(skill))}

        {/* バリアントありスキル */}
        {Object.entries(groupedSkills.withVariants).map(
          ([skillGroup, skills]) => renderVariantSkillCard(skillGroup, skills)
        )}
      </div>
    </div>
  );
};
